.word-typing-container {
    text-align: center;
    color: white;
    margin-top: 20px;
  }
  
  .word-typing-japanese {
    font-size: 48px;
    margin: 20px 0;
  }
  
  .word-typing-english {
    font-size: 40px;
    margin: 10px 0;
    color: lightgray;
  }
  
  .word-typing-input-container {
    display: flex;
    position: relative;
    margin: 20px auto;
    width: 500px;
  }
  
  .word-typing-input {
    padding: 20px;
    font-size: 40px;
    width: 100%;
    text-align: center;
    color: white;
    background-color: #1e1e2f;
    border: 2px solid gray;
    border-radius: 10px;
    outline: none;
    transition: border-color 0.2s ease;
  }
  
  .word-typing-input.correct {
    color: lightgreen;
    border-color: lightgreen;
  }
  
  .word-typing-enter-key {
    position: absolute;
    top: 50%;
    left: 510px;
    transform: translateY(-40%);
    width: 70px;
    height: 70px;
    cursor: pointer;
    transition: transform 0.2s ease;
  }
  
  .word-typing-enter-key:hover {
    transform: translateY(-50%) scale(1.1);
  }
  